.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
  justify-content: center;
  display: flex;
  border-style: none;
  opacity: 0;
  visibility: hidden;
  transition: visibility 0s 0.5s, opacity 0.6s;
}

